@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap);
* {
  font-family: "Roboto", sans-serif;
}
.title {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  text-align: center;
  color: #5a6978;
}

.button-style {
  color: white;
  border: none;
  border-radius: 6.5px;
  width: 400px;
}

@media (max-width: 768px) {
  .button-style {
    width: 300px;
  }
}

.button-style:hover {
  color: white;
}

.button-style:disabled {
  background-color: grey;
}

.button-style:hover:disabled {
  background-color: grey;
}

.light-blue {
  background-color: #0092dd;
}

.light-blue:hover {
  background-color: #017bb8;
}

.green {
  background-color: #77d352;
}

.green:hover {
  background-color: #52c226;
}

.sidebar {
  min-width: 250px;
  max-width: 250px;
  height: 100vh;
  background-color: #0092dd;
  color: white;
  float: left;
  position: fixed;
}

@media (max-width: 767px) {
  .sidebar {
    margin-left: -250px;
  }
  .sidebar.active {
    margin-left: 0px;
  }
}

.sidebar a {
  text-decoration: none;
}

.volunteer_sidebar_image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}

.sidebar h4 {
  margin: 10px auto;
  color: white;
}

.sidebar h6 {
  margin-top: 10px;
  margin-left: 10px;
}

.sidebar button {
  border: none;
  color: white;
  background-color: transparent;
}

.navbar {
  background-color: #0092dd;
  z-index: 1;
  width: 100vw;
}

.navbar button {
  border: none;
  color: white;
  background-color: transparent;
  margin-top: 5px;
}

.navbar .nav-item .active {
  color: white;
}

.nav-item > a {
  text-decoration: none;
}

.loggin-container {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
}

.form-logo {
  width: auto;
  height: 80px;
}

.form-img {
  color: white;
  font-size: 90px;
}



#Google{
