.volunteer_profile_div {
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 100vh;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.user_profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 147, 221, 0.808);
  width: 50%;
  border-radius: 10px;
  margin: 100px auto;
  color: white;
  position: relative;
}

.profileImageBackground {
  opacity: 0.6;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(1px);
          filter: blur(1px);
}

.volunteer_profile_div h2 {
  margin: 30px auto;
  color: white;
}

.volunteer_profile_div button {
  margin: 20px auto;
}

.volunteer_profile_div h5 {
  margin-top: 20px;
  color: white;
}

.volunteer_profile_div p {
  margin-top: 5px;
  color: white;
}

.volunteer_profile_div h4 {
  margin-left: 20px;
  color: white;
}

.volunteer_profile_div .instructions {
  margin-top: 80px;
}

.volunteer_profile_div ul {
  margin-top: 20px;
  list-style-type: square;
}

@media (max-width: 768px) {
  .profileImageBackground {
    display: none;
  }
  .user_profile {
    width: 100%;
    border-radius: 5px;
    margin-top: 20px;
  }
}

.volunteer_content_div {
  padding: 20px;
  display: flex;
  flex-direction: column;
}


.content_div {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: scroll;
}

.content_div::-webkit-scrollbar {
  height: 7px;
}

.content_div::-webkit-scrollbar-track {
  background-color: transparent !important;
}

.content_div::-webkit-scrollbar-thumb {
  background-color: rgb(136, 136, 136, 0.4) !important;
  border-radius: 5px;
  scrollbar-width: 10px;
}

.single_content_div {
  display: flex;
  padding: 10px;
}

.card {
  margin: 5px;
  width: 20rem;
  border-radius: 10px;
}

.circle {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.card > a {
  text-decoration: none;
}

@media (max-width: 786px) {
  .card {
    width: 16rem;
  }
}
